@import "https://fonts.googleapis.com/css?family=Raleway:200";
* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background: url("5053309-min.e107efaa.jpg") center / cover no-repeat fixed;
  margin: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

input {
  font: inherit;
  padding: 8px;
}

.btn {
  height: 60px;
  cursor: pointer;
  color: #fff;
  background: none;
  border-radius: 5px;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 20px;
  padding: 10px 15px;
  font-family: Raleway;
  font-size: 2rem;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.gradient-border {
  --borderWidth: 3px;
  border-radius: var(--borderWidth);
  background: #151515;
  position: relative;
}

.gradient-border:after {
  content: "";
  top: calc(-1 * var(--borderWidth) );
  left: calc(-1 * var(--borderWidth) );
  height: calc(100% + var(--borderWidth) * 2);
  width: calc(100% + var(--borderWidth) * 2);
  border-radius: calc(2 * var(--borderWidth) );
  z-index: -1;
  background: linear-gradient(60deg, #9d0f16, #f79533, #f37055, #e4ef4e, #5073b8, #1098ad, #07b39b, #6fba82) 0 0 / 300% 300%;
  animation: animatedgradient 3s infinite alternate;
  position: absolute;
}

@keyframes animatedgradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

